import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AlertService {
  alert: object = {
    message: ''
  };

  constructor() { }

  public displayAlert(message: string): void {
    this.alert['message'] = message;

    setTimeout(() => {
      this.alert['message'] = '';
    }, 3000)

    return;
  }
}
